<template>
<!-- eslint-disable -->
  <div>
    <TodoList v-bind:todos="todos"/>
    <CreateTodo v-on:create-todo="createTodo"/>
  </div>
</template>
<script>
/* eslint-disable */

import CreateTodo from './CreateTodo'
import TodoList from './TodoList' 
export default {
  name: 'List',
  components:{
    TodoList,
    CreateTodo
  },
  data() {
    return {
      todos: []
    }
  },
  methods:{
    createTodo(newTodo) {
      this.todos.push(newTodo);
     
    }
  }
}
</script>