<template>
  <!-- eslint-disable -->
  <div v-if="isEditing">
    <form method="POST" v-on:submit.prevent="sendData" enctype="multipart/form-data">
      <input 
      type="text"  
      name="name"
      v-model="user.name" 
      placeholder="Nombre" />
      <input
      name="firstname"
        type="text"
        v-model="user.firstname"
        placeholder="Apellido Paterno"
      />
      <input
        type="text"
        v-model="user.lastname"
        placeholder="Apellido Materno"
        name="lastname"
      />
      <input 
      name="genre"
      type="text" 
      v-model="user.genre" 
      placeholder="genero" />
      <input type="number" 
      v-model="user.age" 
      placeholder="Edad" />
      <input 
      name="alergy"
      type="text" 
      v-model="user.alergy" 
      placeholder="alergy" />
      <button type="submit">
        <i class="fas fa-save"></i>
      </button>
    </form>
      <button @click="closeForm">
        <i class="fas fa-window-close"></i>
      </button>
  </div>
</template>
<script>
/* eslint-disable */
import axios from 'axios'
class User {
  constructor (name, firstname, lastname, age, genre, alergy) {
    this.name = name;
    this.firstname = firstname;
    this.lastname = lastname;
    this.age = age;
    this.genre = genre;
    this.alergy = alergy;
  }
}
export default {
  name: "EditProfile",
  data() {
    return {
      isEditing: true,
      user: new User()
    }
  },
  methods:{
    openForm() {
     this.isEditing = true
    },
    closeForm() {
    this.isEditing = false
    },
     sendData() {
      //console.log(this.user)
      const authOptions = {
        method: "POST",
        url: "http://localhost:3000/edit",
        data: JSON.stringify(this.user),
        headers: {
          "Content-Type": "application/json",
          'Accept': "application/json",
          "Access-Control-Allow-Origin": "*"
        },
        json: true,
        withCredentials: true
      };
      axios(authOptions)
        .then(response => {
          this.user = response.data;
          //console.log("Exito",response.data)
        //  this.$router.push('mybrain');
        })
        .catch(error => console.log("error", error));
    },
    }
  }
</script>
