<template>
<!-- eslint-disable -->
<div>
  <div>
    <h3>Completed Tasks: {{todos.filter(todo => {return todo.done === true}).length}}</h3>
    <h3>Pending Tasks: {{todos.filter(todo => {return todo.done === false}).length}}</h3>
    <Todo v-on:delete-todo="deleteTodo" v-on:complete-todo="completeTodo" v-for="todo in todos" v-bind:key="todo.id" :todo.sync="todo"/>
    </div>
  </div>

</template>
<script>
/* eslint-disable */

import Todo from './Todo'
export default {
  name: 'TodoList',
  props:['todos'],
  components: {
    Todo,
  },
   methods: {
    deleteTodo(todo) {
      const todoIndex = this.todos.indexOf(todo)
      this.todos.splice(todoIndex, 1)
    
    
    },
    completeTodo(todo) {
      const todoIndex = this.todos.indexOf(todo);
      this.todos[todoIndex].done = true;
     
    },
  },
};
</script>
<style>
h3{
  color: white;
  font-weight: bold;
}
</style>